.section01-div{
    display: flex;
    flex-direction: row;
    text-align: start;
    justify-content: center;
    align-items: center;
    gap:  10%;
    border-radius: 0.5rem;
    padding: 5%;
    font-family: GmarketSansLight;
}

.PBS_img{
    width: 15rem;
    filter: grayscale(100%);
    overflow: hidden;
}


.profile-about{
    text-align: center;
    background-color: white;
    border-radius: 1rem;
    padding: 1rem;
    color: #ffffff;
    width: 20rem;
    height: 33.5rem;
    z-index: -1;
}

.card_line{
    position: absolute;
    z-index: -1;
    background-color: #ffffff;
    top:-990;
    width: 2rem;
    height: 100vh;
    border-top: 29rem solid rgb(198, 131, 81);
}

.section01_color_box{
    position: absolute;
    z-index: -1;
    background-color: #ffffff;
    border-bottom: 29rem solid rgb(198, 131, 81);
    border-left: 20rem solid rgba(0,0,0,0);  
    border-radius: 1rem; 
}

.section01_color_box_white{
    position: absolute;
    
    z-index: 0;
    border-top: 21rem solid rgb(255, 255, 255);
    border-right: 21rem solid rgba(0,0,0,0);   
}

.insta{
    visibility:initial;
    padding: 0.5rem;
    border: 1px solid rgba(0, 0, 0, 0.100);
    background-color: white;
    border-radius: 0.5rem;
}

.l-font{
    font-size: 2.0rem;
    font-family: GmarketSansBold;
    font-weight: bolder;
}

.profile-about strong{
    font-weight: lighter;
}

.profile-text{
    font-family: GmarketSansMedium;
}

@media screen and (max-width: 1280px) {
    .banner-box img { width: 100%; height: 10rem; }
    /* video::-webkit-media-controls-fullscreen-button { display: none !important; } */

    .section01-div{
        flex-direction: column;
        width: auto;
    }

    .insta{
        display: none;
        visibility: none;
    }

    .section01_color_box{
        position: absolute;
        z-index: -1;
        background-color: #ffffff;
        border-bottom: 29rem solid rgb(198, 131, 81);
        border-left: 20rem solid rgba(0,0,0,0);  
        border-radius: 1rem; 
    }
}