/* Modal styles */

.modal {
    z-index: 999;
    position: fixed;
    background-color: rgb(255, 255, 255);
    border-radius: 0.5rem;
    width: 90vh;
    height: 90vh;
    padding: 1vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    gap: 10rem;

    overflow: auto;
}

.Modal_left{height: 100%; color: black;}

.Modal_right{height: 100%; color: black;}

.viewer {
    text-align: center;
    height: 50vh;
}

.viewer img {
    height: 100%;
    position: relative;
    width: auto;
    border: 1rem solid rgba(0, 0, 0, 0.308);
}

.thumbnails {
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 6%;
    width: 100%;
}

.thumbnails img {
    height: 50%;
}

@media screen and (max-width: 720px) {
    .modal {
        background-color: rgb(255, 255, 255);
        border-radius: 10px;
        width: 43vh;
        height: 60vh;
    }

    .viewer {
        text-align: center;
        height: 50vh;
        font-size: 7vh;
    }

    .viewer img {
        position: relative;
        height: 90%;
        width: 90%;
        border: 0em solid rgba(0, 0, 0, 0.308);
    }

    .thumbnails {
        display: none;
        text-align: center;
        align-items: center;
        justify-content: center;
        height: 10vh;
        width: 100%;
    }
}